/* Define Variables */

$primaryfont: "Work Sans", sans-serif;
$secondaryfont: "Indie Flower", cursive;

$accentcolor: #8686b8;
$beigecolor: #f3f3f2;

$box-shadow: 0 0 21px #b2b2c3;

/* Mixin */

@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

@mixin transform($property) {
    -webkit-transform: $property;
    -ms-transform: $property;
    transform: $property;
  }

@mixin whiteborder {
     box-shadow: $box-shadow;
     border-radius: 4px;
     border: #ffffff 45px solid;
  }

/* CSS reset */

html, body, p, h1, h2, h3 {
    margin: 0px;
    padding: 0px;
    font-weight: normal;
    font-weight: 300;
}

/* Element styles */

body {
    background-color: $beigecolor;
    font-family: $primaryfont;
    letter-spacing: 1.0px;
    background-image: url("./whitemap.png");
    //  background-image: url("./world.svg");
    background-position: top center;
   }

* { box-sizing: border-box; }

h1 {
    color: $beigecolor;
    font-size: 16px;
    letter-spacing: 5px;
    text-transform: uppercase;
    text-align: center;
    line-height: 2.0;

     i {
        padding-right: 20px;
    }

}

h2 { text-transform: uppercase; }


/* Header and Footer */

header, footer {
    @include flex-center;
    padding: 30px;
    background: $accentcolor;
    flex-direction: column;
    gap: 12px;
}

header { margin-bottom: 50px; }

header nav button {
    border: none;
    padding: 10px 20px;
    letter-spacing: 3px;
    font-size: 12px;
    text-transform: uppercase;
    font-family: $primaryfont;
    font-weight: 100;
    background: $beigecolor;
    cursor: pointer;
    margin-right: 20px;
     }

nav button:last-child {
    margin-right: 0px;
    }

footer {
    font-size: 12px;
    color: $beigecolor;
    margin-top: 100px;
}

footer .social-icons a {
    font-size: 18px;
    color: $beigecolor;
    margin: 5px;
}

main, .banner, .container {
    margin: 0 auto;
    max-width: 1150px;
}

/* Breadcrumb */

.timeline-section.bread-search {
    border: #ffffff 25px solid;
    flex-direction: row;
    justify-content: space-between;
    border-left-width: 45px;
    border-right-width: 45px;
}

.breadcrumb .filter-title {
    justify-content: left;
    font-family: 'Work Sans';
    font-size: 14px;
    letter-spacing: 4px;
}

/* Search */

.search { position: relative;}

.search input {
    font-family: "Work Sans";
    background: #f3f3f2;
    padding: 14px 20px 14px 40px;
    font-size: 11px;
    border: none;
    letter-spacing: 1.5px;
    display: inline;
    width: 100%;
    text-transform: uppercase;
}

.search i {
    position: absolute;
    top: 12px;
    left: 12px;
    color: $accentcolor;
    display: inline-block;
}

.search .input-hidden-label {
    height: 0;
    visibility: hidden;
    padding: 0px 20px;
}

.no-results {
    font-size: 18px;
    margin: 45px 20px 0px 20px;
}

/* Card */

.cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 48px;
}

.card {
    display: flex;
    padding: 20px 0px;
    max-width: 929px;
    position: relative;
}

.card_left {
    position: relative;
    border: none;
    padding: 0;
    cursor: pointer;
    @include flex-center;

    .travel_img {
        max-width: 350px;
        height: 515px;
        @include whiteborder;
        border-bottom: 80px #ffffff solid;
      }
}


.card .card_right {
        padding-left: 20px;
        line-height: 2.0;
        @include flex-center;
        flex-direction: column;
        width: 100%;

        > div {
            margin: 5px 0px;
        }
    }

.card .card_right { display: none; }

.card.card-open .card_right {
    display: flex;
    position: absolute;
    background: #ffffff;
    padding: 0px 36px;
    height: 93%;
    gap: 20px;
    cursor: pointer;
 }


.travel_locationData, .travel_dates {
    @include flex-center;
    gap: 10px;
}

.travel_locationData {
    flex-direction: column;
}

.travel_dates {
    text-transform: uppercase;
    font-size: 12px;
}

span.travel_location {
    text-transform: uppercase;
    font-size: 14px;
    margin-left: 7px;
}

span.travel_description {
    text-align: center;
}

.travel_locationData i, .travel_locationData a {
    color: $accentcolor;
}

.travel_locationData a {
    text-underline-offset: 4px;
}

.travel_title {
    font-size: 25px;
    text-transform: capitalize;
    font-size: 1.5em;
}

.travel_title2 {
    font-family: $secondaryfont;
    letter-spacing: 5px;
    position: absolute;
    bottom: 25px;
    font-size: 1.5em;
}


/* Number */

.number {
    font-weight: bold;
    font-size: 30px;
    letter-spacing: 6px;
    color: $accentcolor;
}

.timeline-num {
    display: flex;
    align-items: center;
    font-size: 14px;
    margin: 0 20px;
}

.number-container {
display: flex;
justify-content: space-between;
flex-wrap: wrap;
}



/* Banner */

.banner {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    margin-bottom: 50px;

    > div {
        width: 100%;
        @include flex-center;
        text-align: center;
    }
}

.polaroid-banner {
    position: relative;

    img {
        max-width: 380px;
        @include whiteborder;
        border-bottom: 80px white solid;
    }

}


.ninety-days-banner {
    h2 {
    position: absolute;
    color: $beigecolor;
    @include transform(rotate(-12deg));
    line-height: 3;
    letter-spacing: 6px;
    max-width: 300px;
    font-size: 18px;
    font-family: $secondaryfont;
    }

    img {
        max-width: 420px;
        position: relative;
    }
}

/* Sidebyside components */

.compflex .timeline-section {
    margin: 20px 0px;
}

.compflex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.compflex > div:first-child {
    width: 38%;
}

.compflex > div:last-child {
    width: 60%;
}

/* Map Filter */

.button-map-section {
    @include whiteborder;
    background-color: $beigecolor;
    width: 100%;
    height: auto;
    border-top: white 100px solid;
    border-bottom: white 20px solid;
}

img.button-map { max-width: 100%; }


.map-buttons-container {
    overflow: hidden;
    background: #ffffff;
}

.button-map-section {
    position: relative;
    text-align: center;

     button {
    position: absolute;
    border: none;
    color: $beigecolor;
    background: $accentcolor;
    padding: 10px 15px;
    letter-spacing: 3px;
    font-size: 11px;
    text-transform: uppercase;
    font-family: $primaryfont;
    font-weight: 100;
    cursor: pointer;
   }

    button:focus {
        outline: 4px solid white;
        box-shadow: $box-shadow;
    }



}

.hidden { display: none; font-family: "Indie Flower", cursive; }

.active .hidden {
    display: block;
    position: absolute;
    font-size: 15px;
    font-weight: 400;
    padding-left: 25px;
}

button.deactivate, .deactivate button {
    opacity: 55%;
    pointer-events: none;
}

.timeline-section button.deactivate {
    opacity: 40%;
}

button.active.deactivate {
    box-shadow: 0 0 21px #000000; 
}


/* Zoom Buttons */

.map-buttons {
    transform: scale(1.2);
    transform-origin: top;
}

.zoom-directions {
    position: absolute;
    display: flex;
    left: 105.39px;
} 

.zoom-controls button {
    left: 0px;
    box-shadow: 0px 2px 3px #b2b2c3;
}


.zoom-directions button {
    position: relative;
}

.zoom-controls {
    position: relative;
    z-index: 9;
}

// .map-buttons.zoom-on {
//     transform: scale(1.75);
// }

.map-buttons svg {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
}

.map-buttons {
    position: relative;
    height: 550px;
}

/* Zoom directions */ 

.zoom-on {
    transform: scale(1.8);
}

.map-buttons.zoom-on.zoom-up.zoom-left.zoom-center {
    transform-origin: top left;
}

.map-buttons.zoom-on.zoom-up.zoom-center {
    transform-origin: top;
}

.map-buttons.zoom-on.zoom-down.zoom-center {
    transform-origin: bottom;
}

.map-buttons.zoom-on.zoom-left.zoom-center {
    transform-origin: left;
}

.map-buttons.zoom-on.zoom-right.zoom-center {
    transform-origin: right;
}

.map-buttons.zoom-on.zoom-up.zoom-right.zoom-center {
    transform-origin: top right;
}

.map-buttons.zoom-on.zoom-down.zoom-right.zoom-center {
    transform-origin: bottom right;
}

.map-buttons.zoom-on.zoom-down.zoom-left.zoom-center {
    transform-origin: bottom left;
}

.map-buttons.zoom-on.zoom-center {
    transform-origin: top center;
}


