}


.button-map-section button {
    padding: 10px 20px;
    background: #f3f3f2;
    color: #8686b8;
    font-weight: 400;
}

.button-map-section button.All {
    top: 2px;
    right: 2px; 
}

h2.filter-title {
    position: absolute;
    width: 100%;
    @include flex-center;
    top: -50px;
    font-family: $secondaryfont;
    letter-spacing: 5px;
}

/* Year */

.year-buttons select {
    font-family: "Work Sans";
    letter-spacing: 1.5px;
    font-weight: 100;
    cursor: pointer;
    background: #f3f3f2;
    border: none;
    padding: 14px 45px 14px 45px;
    font-size: 13px;
    text-transform: uppercase;
    appearance: none;
    pointer-events: all;
}

.year-buttons { 
    display: flex; 
    align-items: center; 
    justify-content: center;
    position: relative;
    pointer-events: none;
}

.year-buttons:before {
    font-family: "Font Awesome 6 Free"; 
    content: "\f073";
    font-size: 20px;
    color: $accentcolor;
    margin-bottom: 2px;
    position: absolute;
    left: 12px;
  }

  .year-buttons:after {
    font-size: 20px;
    color: $accentcolor;
    margin-bottom: 2px;
    position: absolute;
    right: 12px;
    font-family: "FontAwesome"; 
    content: "\f078";
  }

/* Timeline section */

.timeline-section {
    @include flex-center;
    flex-direction: column;
    gap: 20px;
    background: #ffffff;
    @include whiteborder;
    margin: 20px auto;

    .filter-title { position: static; }

     button {
        border: none;
        padding: 10px 20px;
        letter-spacing: 3px;
        font-size: 12px;
        text-transform: uppercase;
        font-family: $primaryfont;
        font-weight: 100;
        color: $beigecolor; 
        background: $accentcolor;
        cursor: pointer;
        border-right: 4px solid #ffffff;
    }

      button:last-child { border-right: none; }

}


/* Button focus styles */

:focus { outline: 4px solid white;  box-shadow: 0 0 21px #363646; }

button.active, button:focus {
    outline: 4px solid white;
    border-right: none;
    z-index: 999;
    position: relative;
    box-shadow: 0 0 21px #363646;
   }

   .button-map-section button.active,
   .button-map-section  button:focus {
    position: absolute;
}

.map-buttons-container button.active:not(.All),
.map-buttons-container button:focus:not(.All) {
    color: black;
    outline: none;
    box-shadow: none;
}

.zoom-directions button:focus {
    position: relative;
}

.button-map-section button.active.All {  box-shadow: 0 0 21px $accentcolor;} 

button:hover {
    opacity: 35%;
    background: $accentcolor;
    color: $beigecolor;
}

.timeline-section button:hover  { opacity: 75%; }

/* Mobile Styles */

@media screen and (max-width: 1150px) {
    .cards-container {
        justify-content: space-evenly;
    }

 }


@media screen and (max-width: 768px) {

    .map-buttons.zoom-on.zoom-up.zoom-left.zoom-center {
        transform: none;
    }

    .ninety-days-banner h2 {
        font-size: 4.1vw;
        max-width: 65%;
    }

    .banner, .card {
        flex-direction: column;
    }

    .compflex .timeline-section { margin: 20px auto;}

    .compflex>div:first-child, .compflex>div:last-child {
        width: 90%;
    }

    .card { gap: 40px; }

    .button-map-section {
        background: white;
        border: #ffffff 45px solid;
        box-shadow: 0 0 21px #b2b2c3;
    }

    img.button-map {
        border: white solid 20px;
        box-shadow: none;
    }

    .zoom-controls {
        display: none;
    }

    .button-map-section .filter-title {
        position: static;
    }

    .button-map-section, .timeline-section {
        max-width: 90%;
        margin: 20px auto;

        button, button.active, button:focus {
            position: static;
        }

       button:not(.All) { display: inline ;}

       span.hidden {
        display: block;
        font-size: 14px;
      }

      .active .hidden { position: static; font-size: 14px; padding-left: 0px; }

    }

    .button-map-section button { margin: 6px; }



    .timeline-section .timeline-buttons {
        @include flex-center;
        flex-wrap: wrap;
        gap: 10px;


        button:last-child { border-right: 4px solid #8686b7; }

    }


    .polaroid-banner img, .ninety-days-banner img {  max-width: 90%; }


}

